<template>
	<div class="userinfo-box">
		<ul>
			<li class="clearfix">
				<label class="info-item">技术号名称</label>
				<div class="infocontent">{{ infoname }}</div>
				<div class="action"><a @click="editername = true">修改</a><p>本年度还可以修改两次</p></div>
				<Modal  title="修改技术号名称" v-model="editername" @on-ok="editname" :mask-closable="false" class-name="vertical-center-modal" width="650px">
					<p slot="header" style="text-align:left">
						<Icon type="android-apps"></Icon>
			            <span>修改技术号名称</span>
			        </p>
			        <div class="editername-box clearfix">
			        	<div class="edit-item">
			        		<label>现技术号名称:</label>
			        		<div class="edit-item-info">科技男</div>
			        	</div>
			        	<div class="edit-item">
			        		<label>新技术号名称:</label>
			        		<div class="edit-item-info">
			        			<Input v-model="newname" size="large" placeholder="" style="width: 400px"></Input>
			        		    <span>长度2-15个汉字，不能含有特殊符号、空格，详细查看<a href="">技术号命名规范</a></span>
			        		</div>
			        	</div>
			        	<div class="edit-item tips">
			        		改名须知：
								<li>一、1个自然年内最多可修改2次（如2017年1月1日-2017年12月31日可修改2次）</li>
								<li>二、所修改的名称需在审核通过后生效</li>
								<li>三、新名称生效后，原名称将会被系统收回，其他用户可注册使用</li>
								<li>四、所修改的名称未通过审核，您将仍然使用原名称，您也可以重新修改，审核不通过的修改不占用修改次数</li>
			        	</div>
			        </div>
			    </Modal>
			</li>
			<li class="clearfix">
				<label class="info-item">技术号介绍</label>
				<div class="infocontent">一个有点文艺气质的科技男一个有点文艺气质的科技男一个有点文艺气质的科技男一个有点文艺气质的科技男一个有点文艺气质的科技男</div>
				<div class="action"><a @click="editerdesc = true">修改</a><p>每月只能修改一次</p></div>
				<Modal  title="修改技术号名称" v-model="editerdesc" class-name="vertical-center-modal" width="600px">
					<p slot="header" style="text-align:left">
						<Icon type="android-apps"></Icon>
			            <span>修改技术号介绍</span>
			        </p>
			        <div class="editername-box clearfix">
			        	<div class="edit-item">
			        		<Input  type="textarea" :rows="4" placeholder="Enter something..."></Input>
			        	</div>
			        	<div class="edit-item tips">
			        		对读者描述企鹅号定位或作者简介，简介将显示在媒体主页，请勿发布个人手机号码、邮箱、广告营销信息，请勿超过100字
			        	</div>
			        </div>
			    </Modal>
			</li>
			<li class="clearfix">
				<label class="info-item">技术号头像</label>
				<div class="infocontent"><img src="http://p9.pstatp.com/large/18a2000597d1533e5cb0"><p>尺寸200X200像素，能代表个人、机构或品牌形象，请勿使用低俗图片、二维码、其他机构企业等涉嫌误导用户的头像</p></div>
				<div class="action"><a @click="editerimg = true">修改头像</a></div>
				<Modal  title="修改技术号头像" v-model="editerimg" class-name="vertical-center-modal" width="630px">
					<p slot="header" style="text-align:left">
						<Icon type="android-apps"></Icon>
			            <span>修改技术号头像</span>
			        </p>
			        <div class="editername-box clearfix">
			        	<div class="edit-item">
			        		<div class="edit-item-img">
			        			<Upload action="//jsonplaceholder.typicode.com/posts/" :show-upload-list="false" :format="['jpg','jpeg','png']"
			        				:on-format-error="formaterror"
			        				:before-upload="uploadbefore"
			        				>
						        	<Button type="ghost" icon="ios-cloud-upload-outline">上传图片</Button>
						    	</Upload>
			        		</div>
			        		<div class="infocontent-tips">一个月只能修改一次，请勿使用低俗图片、二维码、其他机构(或个人)标识等涉嫌侵权或误导用户的头像，支持：jpg、jpeg、png格式，大小5M以内</div>
			        	</div>
			        	<div class="edit-item tips">
			        		预览
			        		<div class="view-img">
			        			<div class="view-180">
			        				<img src="../../../build/logo.png">
			        				<p>180X180</p>
			        			</div>
			        			<div class="view-120">
			        				<img src="../../../build/logo.png">
			        				<p>180X180</p>
			        			</div>
			        			<div class="view-120-radius">
			        				<img src="../../../build/logo.png">
			        				<p>180X180</p>
			        			</div>
			        		</div>
			        	</div>
			        </div>
			    </Modal>
			</li>
			<li class="clearfix">
				<label class="info-item">技术号二维码</label>
				<div class="infocontent"><img src="http://inews.gtimg.com/newsapp_ls/0/1151285222/0"></div>
			</li>
			<li class="clearfix">
				<label class="info-item">联系人</label>
				<div class="infocontent">杨**</div>
			</li>
			<li class="clearfix">
				<label class="info-item">联系电话</label>
				<div class="infocontent">183*******</div>
			</li>
			<li class="clearfix">
				<label class="info-item">类型</label>
				<div class="infocontent">个人</div>
			</li>
			<li class="clearfix">
				<label class="info-item">所在地</label>
				<div class="infocontent">甘肃兰州</div>
			</li>
			<li class="clearfix">
				<label class="info-item">联系邮箱</label>
				<div class="infocontent">150****@qq.com</div>
			</li>
		</ul>
	</div>
</template>

<script>
export default {
  data () {
    return {
      editername: false,
      editerdesc: false,
      editerimg: false,
      infoname: '科技男',
      newname: ''
    }
  },
  methods: {
    editname (name) {
      this.infoname = this.newname
      this.$http.post('static/js/data.json?page=' + this.page, {page: this.page}, {emulateJSON: true}).then(
         function (res) {
           for (let i = 0; i <= res.data.result.data.length - 1; i++) {
             this.gridData.push(res.data.result.data[i])
           }
           this.page++
           this.spinShow = false
           this.spinShowloadmore = true
         },
         function (res) {
           console.log('111')
         }
    )
    },
    formaterror (res, file) {
      console.log(res)
      this.$Message.error('选择的文件格式不正确！')
    },
    uploadbefore (res, file) {
      console.log(res)
    }
  }
}
</script>

<style>
	.vertical-center-modal{
        display: flex;
        align-items: center;
        justify-content: center;

        .ivu-modal{
            top: 0;
        }
    }
    .vertical-center-modal .ivu-modal{
    	top: 0;
    }
</style>


